.User-root {
    position: absolute;
    cursor: pointer;
    width: 100%;
    height: 50px;
    border: 1px solid black;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 13px;
    white-space: nowrap;
    background-color: white;
    /*transition: all .2s ease;*/
    -webkit-transition-timing-function: ease, ease-out;
         -o-transition-timing-function: ease, ease-out;
            transition-timing-function: ease, ease-out;
    -webkit-transition-duration: .2s, .4s;
         -o-transition-duration: .2s, .4s;
            transition-duration: .2s, .4s;
    -webkit-transition-property: background-color, top;
    -o-transition-property: background-color, top;
    transition-property: background-color, top;
}

.User-root:hover {
    background-color: #eee;
}

.User-row {
    display: -ms-flexbox;
    display: flex;
    margin: 6px;
}

.User-name {
    font-weight: bold;
}

.User-date {
    color: grey;
}

.User-text {
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}


